import * as React from 'react';
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import ImageListItemBar from '@mui/material/ImageListItemBar';
import ListSubheader from '@mui/material/ListSubheader';
import IconButton from '@mui/material/IconButton';
import InfoIcon from '@mui/icons-material/Info';
import { useQuery } from 'react-query'
import { Button } from '@mui/material';
import { useState } from 'react';
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
type Meme = {
    id: number
    name: string
    url: string
    box_count: number
}


export default function MemeList() {
    const [isClear, setIsClear] = useState(false)
    const { isLoading, error, data: memes } = useQuery({
        queryKey: ['memes', isClear],
        queryFn: () => {
            if (isClear) return null
            return fetch('https://api.imgflip.com/get_memes').then(res => res.json().then(data => data.data.memes))
        }
    })
    return (
        <>
            <ImageList sx={{ width: 500, height: 450 }} className="memeList">
                <ImageListItem key="Subheader" cols={2}>
                    <ListSubheader component="div">December</ListSubheader>
                </ImageListItem>
                {memes?.map((item: Meme) => (
                    <ImageListItem key={item.url}>
                        <img
                            src={`${item.url}?w=248&fit=crop&auto=format`}
                            srcSet={`${item.url}?w=248&fit=crop&auto=format&dpr=2 2x`}
                            alt={item.name}
                            loading="lazy"
                        />
                        <ImageListItemBar
                            title={item.name}
                            subtitle={'id: ' + item.id}
                            actionIcon={
                                <IconButton
                                    sx={{ color: 'rgba(255, 255, 255, 0.54)' }}
                                    aria-label={`info about ${item.name}`}
                                >
                                    <InfoIcon />
                                </IconButton>
                            }
                        />
                    </ImageListItem>
                ))}
            </ImageList>
            <div>
                <Button variant='contained' onClick={() => setIsClear(false)}>Load!</Button>
            </div>
            <div>
                <Button variant='contained' color='error' onClick={() => setIsClear(true)}>Clear</Button>
            </div>
        </>
    );
}

